const AddBankDataToLocal = (payload) => {
  const { id, accountName, accountNumber, swiftCode, address, city, country, firstName, lastName, companyName, type, currency } = payload

  const bankItem = {
    id,
    accountName,
    accountNumber,
    swiftCode,
    address,
    city,
    country,
    firstName,
    lastName,
    companyName,
    type,
    currency
  }

  let oldItems = [];
  oldItems = JSON.parse(localStorage.getItem('bank'));
  oldItems.push(bankItem);
  localStorage.setItem('bank', JSON.stringify(oldItems));
}

export default AddBankDataToLocal
